<template>
  <div class="divBackground">
    {{ list }}
    <div class="docBackground" id="doc" style=" font-family: 'DejaVu Sans Mono';text-align: left;font-size: 20px"
         v-html="msg"
         v-highlight></div>

  </div>
</template>

<script>
export default {
  name: "DocumentSpringCloud",
  data() {
    return {
      md: "",
      // path : ""
      //保存解析后的md文件对象
      msg: '',
      //为了让组件强制渲染
      key: 0,
      list: Object
    }
  },
  methods: {
    //必须设置为异步加载，因为解析需要时间，注意加上async
    testMd(title) {
      // var str = title
      //注意加上await ,运用import函数
      import('../markdown/' + title + '.md').then(myModule => {
        this.msg = myModule.default;
      });
      //强制重新渲染
      this.key += 1
    }
  },
  mounted() {
    this.testMd('Spring');
    this.list = require.context("../markdown/", true, /\.md$/);
  }
}
</script>

<style scoped>
.hljs-line-numbers {
  text-align: right;
  border-right: 1px solid #ccc !important;
  margin-right: 10px !important;
  padding-right: 5px !important;
  color: #999;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

</style>